'use client';

import FollowModal from '@/app/components/followmodal/followModal';
import SeoHead from '@/app/components/seo/seoHead';
import { get_case_studies_paginated } from '@/app/services/homePageService';
import Link from 'next/link';
import { useEffect, useState } from 'react';

type CaseStudy = {
  documentId: string;
  main?: {
    title?: string;
    sub_title?: string;
  };
  slug: string;
  view1: Array<{ image: { url: string } }>;
  cover_image?: string;
};

// In-memory cache for case studies
let cachedCaseStudies: CaseStudy[] | null = null;

export default function Portfolio() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState<CaseStudy[]>([]);
  const [isFollowed, setIsFollowed] = useState(false);
  const [showModal, setShowModal] = useState(false);

  const VIEW_ALL_ITEM = { id: 'view-all', label: 'View all' };

  useEffect(() => {
    const followed = sessionStorage.getItem('isFollowed');
    setIsFollowed(!!followed);

    if (cachedCaseStudies) {
      setData(cachedCaseStudies);
      return;
    }

    const fetchCaseStudies = async () => {
      try {
        setLoading(true);
        const response = await get_case_studies_paginated();
        const caseStudies = response || [];
        setData(caseStudies);

        // Cache in memory
        cachedCaseStudies = caseStudies;
      } catch (error) {
        console.error('Error fetching case studies:', error);
      } finally {
        setLoading(false);
      }
    };

    fetchCaseStudies();
  }, []);

  // Page-level SEO
  const seoTitle = 'Insonix Portfolio - Case Studies';
  const seoDescription =
    'Explore our portfolio of case studies showcasing Insonix’s top AI and Python solutions in action.';
  const seoKeywords = 'Insonix, Portfolio, Case Studies, AI developers, Python developers, solutions';
  const seoUrl = `${process.env.NEXT_PUBLIC_BASE_URL || 'https://insonix.com'}/portfolio`;
  const seoImage = '/default-og.jpg.png';

  if (loading) {
    return (
      <>
        <SeoHead title={seoTitle} description={seoDescription} keywords={seoKeywords} image={seoImage} url={seoUrl} />
        <div className="flex flex-wrap justify-center gap-6 mt-10 mb-10">
          {Array.from({ length: 4 }).map((_, i) => (
            <div
              key={i}
              className="group w-[276px] h-[424px] relative overflow-hidden shadow-lg rounded-lg bg-gray-200 animate-pulse"
            >
              <div className="absolute inset-0 bg-gray-300"></div>
              <div className="absolute inset-0 bg-black/10"></div>
              <div className="relative z-10 h-full flex flex-col justify-between p-6">
                <div className="w-3/4 h-8 bg-gray-400 rounded"></div>
                <div className="w-32 h-4 bg-gray-400 rounded self-end"></div>
              </div>
            </div>
          ))}
        </div>
      </>
    );
  }

  return (
    <>
      <SeoHead title={seoTitle} description={seoDescription} keywords={seoKeywords} image={seoImage} url={seoUrl} />

      <section id="portfolio" className="py-16 md:py-24 dark:bg-primary bg-white">
        {showModal && (
          <FollowModal
            onFollowed={() => {
              setIsFollowed(true);
              setShowModal(false);
            }}
          />
        )}

        <div className="container mx-auto text-center">
          <p className="text-sm font-medium text-gray-400 mb-2 uppercase tracking-wider">Previous Work</p>
          <h1 className="text-3xl md:text-5xl dark:text-white font-extrabold leading-tight">
            Real Transformations in Action
          </h1>

          <div className="flex flex-wrap justify-center gap-6 mt-10">
            {data.map((item: CaseStudy, idx: number) => {
              const caseStudyUrl = `/solutions/startup/${item.slug}`;
              const caseStudyTitle = item?.main?.title || 'Case Study';
              const caseStudyImage = item?.view1?.[0]?.image?.url || '/tech.jpg';

              return (
                <Link
                  key={idx}
                  href={caseStudyUrl}
                  className="group w-[276px] h-[424px] relative overflow-hidden shadow-lg cursor-pointer block"
                  title={caseStudyTitle}
                >
                  {/* Background Image */}
                  <div
                    className="absolute inset-0 bg-cover bg-center transition-transform blur-[2px] group-hover:blur-none duration-500 group-hover:scale-110"
                    style={{ backgroundImage: `url(${caseStudyImage})` }}
                  ></div>

                  {/* Overlay */}
                  <div className="absolute inset-0 bg-gradient-to-b from-secondary  to-secondary/60 dark:from-primary/80 dark:to-primary/80 group-hover:from-black/60 group-hover:to-black/0 transition-all duration-500" />

                  {/* Content */}
                  <div className="relative  h-full flex flex-col justify-between group-hover:text-white  p-6 ">
                    <h1 className="text-lg font-bold hover:text-white dark:text-white">{caseStudyTitle}</h1>
                    <div className="w-full flex justify-end">
                      <span className="inline-block rounded-lg bg-primary/50 border-gray-500  px-5 py-2 text-white dark:text-white hover:border-primary-100 hover:shadow-[0_8px_30px_rgba(33,84,255,0.25),_0_0_20px_rgba(75,201,255,0.22)] transition-all duration-250 ease opacity-0 group-hover:opacity-100 translate-y-2 group-hover:translate-y-0 transition-all duration-300 text-sm font-semibold uppercase text-pr">
                        View Case Study →
                      </span>
                    </div>
                  </div>
                </Link>
              );
            })}
          </div>
        </div>
      </section>
    </>
  );
}
